.country {
	width: 100%;
	display: grid;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	background: #141e30;
	background: -webkit-linear-gradient(170deg, #e9967a, #1ebea5);
	background: -webkit-linear-gradient(280deg, #e9967a, #1ebea5);
	background: -o-linear-gradient(280deg, #e9967a, #1ebea5);
	background: linear-gradient(170deg, #e9967a, #1ebea5);
	color: #1e1e1e;
	font-family: 'Poppins';
	border-radius: 15px;
	-webkit-transition: all 1s ease-in-out;
	-o-transition: all 1s ease-in-out;
	transition: all 1s ease-in-out;
	cursor: pointer;
	margin-top: 4rem;
}

.country:hover {
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}

.qty {
	color: brown;
	font-size: small;
	font-weight: bold;
}

.country > img {
	width: 33%;
	height: auto;
	margin: auto;
}

.country > h2 {
	text-align: center;
	margin: 1.3rem;
}

.country p {
	font-weight: bold;
	margin-bottom: 1.3rem;
}

.cases {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 0.8rem;
	margin: auto;
	width: 90%;
	font-family: 'Poppins';
	align-self: center;
}

@media screen and (orientation: portrait) and (max-width: 767px) {
	.search-box {
		width: 100%;
	}

	.search {
		font-size: 1.2rem;
	}
}



